#demo{
    background: linear-gradient(112deg, #6b6969 50%, rgb(21, 27, 83) 50%);
    max-width: 900px;
    margin: auto;
}
.carousel-caption {
    position: static;
    position: initial;
    z-index: 10;
    padding: 5rem 8rem;
    color: rgba(253, 253, 253, 0.856);
    text-align: center;
    font-size: 1.2rem;
    font-style: italic;
    font-weight: 500;
    line-height: 2rem;
    font-family: "Nunito";
}
@media(max-width:767px){
    .carousel-caption {
        position: static;
        position: initial;
        z-index: 10;
        padding: 3rem 2rem;
        color: rgba(253, 253, 253, 0.856);
        text-align: center;
        font-size: 0.7rem;
        font-style: italic;
        font-weight: bold;
        line-height: 1.5rem;
    }
}
.carousel-caption img{
    width: 6rem;
    border-radius: 5rem;
    margin-top: 2rem;
}
@media(max-width:767px){
    .carousel-caption img{
        width: 4rem;
        border-radius: 4rem;
        margin-top: 1rem;
    }
}
#image-caption{
    font-style: normal;
    font-size: 1rem;
    margin-top: 0.5rem;
}
@media(max-width:767px){
    #image-caption{
        font-style: normal;
        font-size: 0.6rem;
        margin-top: 0.5rem;
    }
}
i{
